<template>
    <div class="all">
        <el-button @click="random">随机一个</el-button>
        <el-carousel :autoplay="false" type="card" height="400px" ref="carouselOly" indicator-position="none" @change="carouselChange" class="carousel" @click.native="selected">
            <el-carousel-item v-for="item in dataset" :key="item.Year + item.Season">
                <el-card class="box-card" shadow="always">
                    <div slot="header" class="clearfix">
                        <span> {{ item.赛事名称 }}</span>
                    </div>
                    <el-image :src="item.imgUrl">
                        <div slot="error" class="image-slot">
                            <fiveRound :name="item.赛事名称" style="width:141px;height:141px" />
                        </div>
                    </el-image>
                </el-card>
            </el-carousel-item>
        </el-carousel>
        <swiper ref="swiper" :options="swiperOption" class="swiper" @touchEnd="transitionStart">
            <!-- slides -->

            <swiper-slide v-for="(item, index) in dataset" :key="'步骤条-' + item.Year + item.Season">
                <div :class="{ text: true, Summer: item.Season == 'Summer', Winter: item.Season == 'Winter' }">
                    {{ (item.Season == "Summer" ? "夏季 " : "冬季 ") + item.赛事名称 }}
                </div>
            </swiper-slide>
        </swiper>

        <el-dialog :title="dataset[select].赛事名称" :visible.sync="dialogVisible" width="70%" :modal="false" custom-class="animate__animated animate__bounceIn" :close-on-click-modal="false">
            {{ dataset[select].届数 }}
            {{ dataset[select].主办城市 }}
            {{ dataset[select].举办国家 }}
            {{ dataset[select].举办时间 }}
            {{ dataset[select].口号 }}

            {{ dataset[select].Year >= 1994 ? (dataset[select].Season == "Summer" ? "夏季奥运会" : "冬季奥运会") : "" }}
            <el-row>
                <el-col :span="16">
                    <p class="description">{{ dataset[select].text || "" }}</p>
                    <el-link type="success" style="float: right" :href="'https://baike.baidu.com/item/' + dataset[select].赛事名称">
                        ——来自百度百科
                        <i class="el-icon-link"></i>
                    </el-link>
                </el-col>
                <el-col :span="8">
                    <el-image :src="dataset[select].imgUrl">
                        <div slot="error" class="image-slot">
                            <fiveRound :name="dataset[select].赛事名称" style="width:141px;height:141px" />
                        </div>
                    </el-image>
                </el-col>
            </el-row>

            <span slot="footer" class="dialog-footer">
                <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
    import fiveRound from "./fiveRound.vue";
    export default {
        components: {
            fiveRound,
        },
        data() {
            return {
                swiperOption: {
                    slidesPerView: 21, // 显示数量
                    freeMode: true,
                    height: 200,
                    centeredSlides: true,
                },
                dataset: [{}],
                select: 0,
                dialogVisible: false,
                active: 1,
            };
        },
        async mounted() {
            this.dataset = await fetch("./奥运会赛事统一数据最终添加赛事介绍.json").then((res) => res.json());
            this.random();
        },
        methods: {
            carouselChange(index) {
                this.$refs.swiper.$swiper.slideTo(index, 1000, false);
            },
            random() {
                this.$refs.carouselOly.setActiveItem(Math.floor(Math.random() * (this.dataset.length + 1)));
            },
            selected() {
                this.select = this.$refs.carouselOly.activeIndex;
                this.dialogVisible = true;
            },
            transitionStart(i) {
                console.log(i.activeIndex);
                this.$refs.carouselOly.setActiveItem(i.activeIndex);
            },
        },
        props: [],
        watch: {},
    };
</script>
<style scoped lang="less">
    .all {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
    .description {
        text-align: left;
        text-indent: 2em;
        line-height: 1.3em;
        letter-spacing: 0ch;
    }
    .box-card {
        min-height: 300px;
        .el-card__body {
            height: 100%;
            align-items: center;
        }
    }
    .swiper {
        text-align: left;
        display: block;
        overflow-x: hidden;
        font-family: cursive;
        width: 100vw;
        height: 200px;
        .swiper-slide {
            writing-mode: vertical-rl;
            .text {
                overflow: visible;
                width: 1em;
                cursor: default;
                user-select: none;
                text-overflow: ellipsis;

                white-space: nowrap;
            }

            .Summer {
                color: brown;
            }
            .Winter {
                color: darkgray;
            }
        }
    }
    .swiper-slide-active > .text {
        transition: 1s;
        font-weight: bold;
        color: Gold !important;
    }

    .carousel {
        width: 800px;
    }
</style>
